.applications-filters {
  &__search {
    margin-block-end: 18px;
  }

  &__accordion {
    margin-block-end: 20px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    gap: 23px 26px;
    margin-block-end: 20px;
  }

  &__actions {
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    gap: 10px;
  }
}
